.fullScreenMask {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, .6);
}

.text {
    font-size: 40px;
    color: #fff;
    padding: 5px 15px;
    border: 3px solid #fff;
    border-radius: 10px;
}